body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
  }
  
  li {
    float: left;
  }
  
  li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }
  
  /* Change the link color to #111 (black) on hover */
  li a:hover {
    background-color: #111;
  }
.home {
    padding: 20px;
    margin: 20px;
}

.home a{
    background-color: #08d;
    border-radius: 12px;
    border: 0;
    box-sizing: border-box;
    color: #eee;
    cursor: pointer;
    font-size: 18px;
    height: 50px;
    margin-top: 38px;
    outline: 0;
    text-align: center;
    padding: 10px;
    margin-right: 10px;
    text-decoration: none;
}
.form-container {
    display: flex;
    padding: 20px;
    align-items: center;
    justify-content: center;
}

form {
    background-color: #15172b;
    border-radius: 20px;
    box-sizing: border-box;
    height: 100%;
    padding: 20px;
    width: 320px;
  }

h2  {
    color: #eee;
    font-family: sans-serif;
    font-size: 36px;
    font-weight: 600;
    margin-top: 30px;
  }

.username {
    color: #eee;
    font-size: 18px;
}
  
 input{
    height: 50px;
    position: relative;
    width: 100%;
    background-color: #303245;
    border-radius: 12px;
    border: 0;
    box-sizing: border-box;
    color: #eee;
    font-size: 18px;
    outline: 0;
    padding: 4px 20px 0;
  }
  
label {
    color: #eee
  }
  
    
button{
    background-color: #08d;
    border-radius: 12px;
    border: 0;
    box-sizing: border-box;
    color: #eee;
    cursor: pointer;
    font-size: 18px;
    height: 50px;
    margin-top: 38px;
    outline: 0;
    text-align: center;
    width: 100%;
  }
  
button :active {
    background-color: #06b;
  }
  
.company-list {
    margin: 20px;
    padding: 10px;
}

.company-card {
    border: 2px solid black;
    border-radius: 10px;
    margin: 4px;
    padding: 10px;
}

.company-card a {
    color: black;
    text-decoration: none;
}

.company-card:hover {
    background-color: rgb(169, 224, 249);
}

.company-detail {
    margin: 20px;
    padding: 10px;
    border: 2px solid black;
    border-radius: 10px;
}
.LoadingSpinner {
    margin: 200px auto 0 auto;
    color: tomato;
    font-size: 24pt;
    font-weight: bold;
    text-align: center;
  }
.job-card {
    border: 2px solid black;
    border-radius: 10px;
    margin: 4px;
    padding: 10px;
}

.job-card a {
    color: black;
    text-decoration: none;
}

.job-card:hover {
    background-color: rgb(169, 224, 249);
}

#apply-btn {
    display: inline;
    width: 20%;
    height: 25%;
    float: right;
}

#apply-btn:disabled {
    color: black;
}
